.container.pd{
  padding:0px 15px 0px 15px;
}
.card_user.card-panel{
  background: #FFFFFF;
  box-shadow:none;
  border-radius: 10px;
  z-index:0 ;
  position: relative;
}

.card_user.card-panel:hover{
  background: #fff ;
  box-shadow: 0 7px 18px 2px #E4E4E4;
  border-radius: 10px;
  z-index:1 ;
  position: relative;
}





td.r{
    padding: 15px 5px;
    display: table-cell;
    text-align: right !important;
    vertical-align: none !important;
    border-radius: 0px !important;
}

.type-text{
  font-size: 12px !important;
  line-height: 30px !important;
  margin:0 !important;
  font-weight: 100 !important;
  height:0px !important;
}


.input-field.add_user {
    position: relative;
    margin-top: 1rem;
    margin-bottom: 0rem !important;
}
