*{
  /* margin:0px; */
  /* align:0px; */
}

hr{
  color:#DEDEDE;
  display: block;
  unicode-bidi: isolate;
  margin-block-start: 10px;
  margin-block-end: 5px;
  margin-inline-start: auto;
  margin-inline-end: auto;
  overflow: hidden;
  border-style: solid;
  border-width: 0.5px;
}

h1{
  font-size: 20px !important;
  line-height: 30px !important;
  margin:0 !important;
  font-weight: 600 !important;
}

h2{
  font-size: 15px !important;
  line-height: 30px !important;
  margin:0 !important;
  font-weight: 600 !important;
}

p{
  font-size: 12px !important;
  line-height: 30px !important;
  margin:0 !important;
  font-weight: 100 !important;
}

td{
    padding: 0px 5px !important;
    display: table-cell;
    vertical-align: none !important;
    border-radius: 2px;
}

table {
    width: 100%;
    display: table;
    border-collapse: unset !important;
    border-spacing: 0;
}

.p_action{
  font-size: 12px !important;
  line-height: 30px !important;
  margin:0 !important;
  font-weight: 100 !important;
  color:#0A86DC;
  cursor:pointer;
}

.p_title{
  font-size: 12px !important;
  line-height: 30px !important;
  margin:0 !important;
  font-weight: 100 !important;
  color:#A1A1A1;
}

.p_green{
  font-size: 12px !important;
  line-height: 30px !important;
  margin:0 !important;
  font-weight: 100 !important;
  color:#7ED321;
}


.row {
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 15px !important;
}

.m-t-50{
  margin-top:50px;
}

.m-t-20{
  margin-top:20px;
}

.alert{
  font-size: 12px !important;
  line-height: 30px !important;
  margin:0 !important;
  font-weight: 400 !important;
}

.hover_red:hover{
  color:#b71c1c;
}

.test{
  border:1px solid black;
}
